import React from 'react';

const Todolist =(props)=>{

   
   
    return (
        <> 
          <div className="todo_style">
              
         <li className="usertask"> {props.text} </li>
        <div className="iconcontainer">     <i className= "fa fa-times" 
              aria-hidden="true" 
              onClick={()=>{
                  props.onSelect(props.id)
              }}/>
              </div>
           </div>
         </>
    );
};




export default Todolist;